@use'base/variables' as v;
@use'base/mixins' as m;

.objetivos{
  margin-top: 5rem;
  &__heading{
    @include m.heading;
  }
  &__grid{
    @include m.tablet { 
      @include m.grid(2, 2rem);
    }
  }
  &__img{
    width: 35rem;
    margin: 0 auto;
    border-radius: 2rem;
  }
  &__listas{
    margin-top: 2rem;
  }

}

.lista{
  display: flex;
  gap: 1rem;
  align-items: center;
  padding-top: 2rem;
  padding-bottom: 3rem;
  font-size: 1.8rem;
}