@use'base/variables' as v;
@use'base/mixins' as m;

.contacto{
  margin: 10rem auto;

  &__heading{
    @include m.heading;
  }
  &__grid{
    grid-template-columns: repeat(1, 1fr);

    @include m.tablet { 
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      justify-items: center;
    }


  
  }

  &__lista{
padding: 2rem 0;
  }
  &__mapa{
    margin: 5rem auto;
    border: solid 1px v.$primario;
    border-radius: 1rem;
  }
}

.mapa{
  width: 100%;
  border-radius: 1rem;
}