html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #3b4862;
}

/** Media Queries **/
.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 15rem 0;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 20rem;
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__heading {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__texto {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}
.header__imagen {
  display: none;
}
@media (min-width: 768px) {
  .header__imagen {
    display: block;
    position: absolute;
    right: 10rem;
    bottom: -15rem;
    max-width: 35rem;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .header__imagen {
    max-width: 44rem;
  }
}
.header span {
  padding-top: 2rem;
  display: block;
  line-height: 1;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
}
.navegacion__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 2rem;
}
.navegacion__link:hover {
  transform: scale(1.1);
  transition-duration: 0.5s;
}
.navegacion__linklast-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

