.active {
  color: rgb(125, 154, 233);
  border: solid 1px rgb(125, 154, 233);
  padding: 1rem 2rem;
  border-radius: 2rem;
}

.servicios {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .servicios {
    margin-top: 10rem;
  }
}
.servicios__heading {
  font-weight: 400;
}
.servicios__heading span {
  font-weight: 700;
}
.servicios__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 51%, #6277a1 100%);
}
.servicios__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.servicio {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.servicio__imagen {
  aspect-ratio: 10/9;
  background-size: cover;
  position: relative;
}
.servicio:nth-child(1) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-pintura.jpg);
}
.servicio:nth-child(2) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-electricidad.jpg);
}
.servicio:nth-child(3) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-retaol.jpg);
  background-position: bottom;
}
.servicio:nth-child(4) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-obrasciviles.jpg);
}
.servicio:nth-child(5) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-drywall.jpg);
  background-position: bottom;
}
.servicio:nth-child(6) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-estructuras.jpg);
}
.servicio:nth-child(7) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicios-sanitarios.jpg);
}
.servicio:nth-child(8) .servicio__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/servicio-aci.jpg);
}
.servicio__nombre {
  position: absolute;
  color: #FFFFFF;
  left: 1rem;
  bottom: 0;
  font-size: 2.4rem;
}
.servicio__contenido {
  padding: 2rem;
}
.servicio__texto {
  font-size: 1.6rem;
  line-height: 2.4rem;
}
.servicio__enlace {
  background-color: #3b4862;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin-top: 2rem;
  display: block;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .servicio__enlace {
    width: auto;
  }
}
.servicio__enlace:hover {
  background-color: #283142;
}

.agregado {
  margin-top: 5rem;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/agregado.jpg);
  background-size: 100%, 100%;
  background-position: center, 0rem 16rem;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .agregado {
    background-position: center, 0rem -2rem;
  }
}
@media (min-width: 480px) and (min-width: 768px) {
  .agregado {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/agregado.jpg);
    background-size: 100%, 70%;
    background-position: center, -10rem 1rem;
  }
}
@media (min-width: 480px) and (min-width: 768px) and (min-width: 1024px) {
  .agregado {
    background-position: center, -18rem -10rem;
  }
}
.agregado__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .agregado__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.agregado__contenido {
  padding: 5rem;
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .agregado__contenido {
    padding: 10rem 5rem;
    grid-column: 2/3;
  }
}
@media (min-width: 768px) and (min-width: 1024px) {
  .agregado__contenido {
    padding: 12rem 6rem;
  }
}
.agregado__heading {
  color: #FFFFFF;
  text-transform: uppercase;
}
.agregado__texto {
  color: #FFFFFF;
  font-size: 1.8rem;
  line-height: 1.7;
  padding: 1rem 0;
}
.agregado__enlace {
  margin-top: 5rem;
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #3b4862;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .agregado__enlace {
    width: auto;
  }
}
.agregado__enlace:hover {
  background-color: #e6e6e6;
}

