@use'base/variables' as v;
@use'base/mixins' as m;

.calidad{
  margin: 10rem auto;
  
  
  &__heading{
    @include m.heading;
  }

  &__titulo{
    line-height: 1.5;
    text-align: justify;
    padding: 1rem 2rem;
    @include m.tablet { 
      padding: 5rem 10rem;
    }
      
  }
  &__imagen{
    width: 30rem;
    margin: 0 auto
  }

  &__grid{
    @include m.grid(1, 2rem);
    @include m.tablet { 
      @include m.grid(3, 10rem);
    }
  }

}

.principio{
  line-height: 1.3;
  background-color: v.$grisClaro;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 0 2rem;
  border: solid 4px black;
  border-radius: 1rem;
  &:hover{
    transform: scale(1.1);
    transition-duration: .3s;
  }

}