@use'base/variables' as v;
@use'base/mixins' as m;

.misionVision{
  margin-top: 5rem;
  &__heading{
    @include m.heading;
  }
  &__grid{
    padding-top: 5rem;
    @include m.grid(1, 2rem);
    @include m.tablet { 
      @include m.grid(2, 2rem);
    }
  }
  &__texto{
    line-height: 1.5;
    text-align: justify;
  }
  &__img{
    aspect-ratio: 10/9;
    background-size: cover;
    position: relative;

  }
  &__valores{
    display: none;
    @include m.tablet { 
      display: block;
    }
  }
    
}

