@use'base/variables' as v;
@use'base/mixins' as m;

.clientes{
  margin: 10rem auto;

  &__heading{
    @include m.heading;
  }

  &__grid{
    @include m.tablet { 
      @include m.grid(5,1rem);
    }
  }
}

.cliente{
  &__imagen{
    &:nth-child(10){
      grid-column: 1 / 6;
      grid-row: 7 / 8;
    }

  }
}