@use'base/variables' as v;
@use'base/mixins' as m;

.serviciosPrincipal{
  margin: 10rem auto;
  &__servicio{
    padding: 5rem 0;
    border-bottom: dotted 3px v.$negro;
    &:last-child{
      border-bottom: none;
    }
  }
  &__heading{
    @include m.heading;
    margin-bottom: 5rem;
  }
  &__flex{
    @include m.tablet { 
      @include m.grid(2,2rem);
    }
    

  }
}