@use'base/variables' as v;
@use'base/mixins' as m;
.servicios{
  margin-top: 5rem;

  @include m.tablet { 
    margin-top: 10rem;
  }

  &__heading{
    @include m.heading;
  }

  &__grid{
      @include m.grid(1, 2rem);
      @include m.tablet { 
        @include m.grid(3, 2rem);
      }
    
  }
}

.servicio{
  background-color: v.$blanco;
  display: flex;
    flex-direction: column;
    justify-content: space-between;
  &__imagen{
    aspect-ratio: 10/9;
    background-size: cover;
    position: relative;
  }
  &:nth-child(1) &__imagen{

    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-pintura.jpg);
  }
  &:nth-child(2) &__imagen{

    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-electricidad.jpg);
    
  }

  &:nth-child(3) &__imagen{
    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-retaol.jpg);
    background-position: bottom;
  }

  &:nth-child(4) &__imagen{

    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-obrasciviles.jpg);
  }

  &:nth-child(5) &__imagen{

    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-drywall.jpg);
    background-position: bottom;
  }
  &:nth-child(6) &__imagen{
    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-estructuras.jpg);
  }
  &:nth-child(7) &__imagen{
    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicios-sanitarios.jpg);
  }
  &:nth-child(8) &__imagen{
    background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%) ,url(../img/servicio-aci.jpg);
    
  }
  &__nombre{
    position: absolute;
    color: v.$blanco;
    left: 1rem;
    bottom: 0;
    font-size: 2.4rem;
  }

  &__contenido{
    padding: 2rem;
  }
  &__texto{
    font-size: 1.6rem;
    line-height: 2.4rem;
  }

  &__enlace{
    @include m.boton(v.$primario, v.$blanco);
    margin-top: 2rem;
    display: block;
    text-transform: uppercase;
  }


}