@use'base/variables' as v;
@use'base/mixins' as m;

.header{
  background-image: linear-gradient(to right, transparentize(v.$primario, .1) 0%, transparentize(v.$primario, .1) 100%), url(../img/header_bg.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  position: relative;
  @include m.tablet { 
    padding: 3rem 0 15rem 0;
  }
  &__barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet { 
      flex-direction: row;
      justify-content: space-between;
    }
    
  }

  &__logo{
    width: 20rem;
  }

  &__contenido{

    @include m.tablet { 
      margin-top: 10rem;
      max-width: 50rem;
    }
  }

  &__heading{
    color: v.$blanco;
    text-align: center;
    @include m.tablet { 
      text-align: left;
    }
      
    
  }
  &__texto{
    color: v.$blanco;
    display: none;
    @include m.tablet { 
      display: block;
    }
      
  }
  &__imagen {
    display: none;
    @include m.tablet { 
      display: block;
      position: absolute;
      right: 10rem;
      bottom: -15rem;
      max-width: 35rem;
      @include m.desktopXL { 
        max-width: 44rem;
      }
    }
  }
  span{
    padding-top: 2rem;
    display: block;
    line-height: 1;
  }
  
}

.navegacion{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;

  @include m.tablet { 
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }

  &__link{
    color: v.$blanco;
    font-size: 1.8rem;
    margin-bottom: 2rem;
    &:hover{
      transform: scale(1.1);
      transition-duration: 0.5s;
    }

    &last-of-type{
      margin-bottom: 0;
    }
    @include m.tablet { 
      margin-bottom: 0;
    }
  }
}

